.product-variant{padding:25px}.product-variant__content{border-top:1px solid #999999;margin-top:15px}.product-variant__content .item{margin-top:15px;margin-bottom:60px;padding:4px;border:1px solid #F0F0F0}.product-variant__content .item img{width:50px;aspect-ratio:1;margin-right:11px}.product-variant__product{padding:25px}.product-variant__product-content{padding-top:15px}.product-variant__product-content img{margin-right:15px;width:65px;aspect-ratio:65/79;object-fit:cover}.product-variant__product-name{font-size:14px}.product-variant__border{box-shadow:0 1px 10px rgba(0,0,0,0.25)}.product-variant__create-new{padding:25px}.product-variant__create-new .form-group{padding:15px 0 0 0;margin-bottom:0}.product-variant-image{padding-top:15px}.product-variant-image__link .uppy-DragDrop-label{color:#7367E0}.product-variant-image__link .uppy-DragDrop-arrow{display:none}.product-variant-image__link .uppy-DragDrop-container{border:none;background-color:transparent}.product-variant-image__link .uppy-DragDrop-container:focus{outline:none}.product-variant-image__container{border:1px solid #999999;box-sizing:border-box;border-radius:5px;padding:10px;min-height:185px}.product-variant-image__container-grid{display:grid;grid-template-columns:1fr 1fr 1fr 1fr}.product-variant-image__container .empty{display:flex;align-items:center;justify-content:center;position:relative;height:163px;margin:0;background-color:transparent;width:100%}.product-variant-image__container .empty-image img{width:70px}.product-variant-image__container .empty-image p{color:#7367E0;margin-bottom:0}.product-variant-image__container .empty button{width:100%;height:100%;position:absolute;top:0;left:0;opacity:0}.title{font-size:16px;margin-bottom:0}.bg-grey{background-color:#F5F5F5}.btn-icon-top{transform:rotate(-90deg)}.btn-icon-bottom{transform:rotate(90deg)}
